import React from "react";
import Layout from "../components/Layout";
import animationData from "../lotties/60674-developers-moods";
import Lottie from "react-lottie"

function Home() {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice"
    }
  };
  return (
    <Layout>
      <div>
        <div className=" h-screen bg-theme_color items-center">
          <div className="grid md:grid-cols-1 grid-cols-2 h-screen items-center">
            <div className="h-1/2">
              <Lottie
              options={defaultOptions}
              height={600}
              width={600}
              />
            </div>
            <div className="font-bold text-white md:text-center sm:text-center	 "> 
              <h1 className="text-7xl text-yellow-400 md:text-4xl">Hiii, <i className="text-white">I am </i> <b className=" text-red-500">ASHUTOSH..</b></h1>
              <h1 className="text-4xl text-white md:text-2xl">FuLL STACK<b className="  text-red-900"> DEVeloper</b></h1>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default Home;
